<template>
    <div class="content-header">
        <h2><span v-text="title"></span></h2>
        <p class="content-header-time"><span></span></p>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        computed:mapGetters({
            title:'title'
        })
    }
</script>


