<template>
    <transition name="animation-prompt">
        <div v-show="showSerial" class="common-ui-pop_move z-index" data-pop >
            <div v-on:touchstart="popTouchstart($event)" v-on:touchmove="popTouchmove($event)" ref="pop_move" class="common-ui-pop-content_move">
                <div @click="hideSerial" class="close">X</div>
                <div class="u-w-9">
                    <label>請輸入產品序號：<input type="text" v-model="serial" ref="serial"></label>
                    (<span>1</span>/<span>66</span>)
                </div>
                <div class="common-ui-pop-btn">
                	<a @click="request_serial" class="common-btn">確認</a>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex'
    import { check_input_null , postRequest , move_pop_touchstart , move_pop_init_startData , move_pop_touchmove } from '../../vuex/utils.js'
    export default {
        data () {
            return {
                startData : {
                    initTop : 0,
                    initLeft : 0,
                    startTop : 0 , 
                    startLeft : 0 , 
                    startX : 0 , 
                    startY : 0 
                } ,
                showSerial : false ,
                serial : ''
            }
        } , 
		// props : {
		//     serialMsg: {type: Object },
		//     isShowSerial : {}
		// },
		props :['serialMsg','isShowSerial'],
        created : function() {
        	this.showSerial = this.isShowSerial;
        },
        watch : {
            'isShowSerial' (to , from) { 
                if ( to == false ) {
                	this.hideSerial();
                } else { 
                    this.showSerial = true;
                }
            }
        } , 
        methods:{
            hideSerial () {
                this.init_move_coordinate() ;
                this.showSerial = false ; 
            } , 
            init_move_coordinate ( ) {
                var vm = this;
                setTimeout(function(){
                    move_pop_init_startData(  vm.$refs.pop_move , vm.startData) ; 
                },500);
            } , 
            popTouchstart ( evt ) {
                move_pop_touchstart( evt , this.$refs.pop_move , this.startData ); 
            } , 
            popTouchmove ( evt ) {
                move_pop_touchmove( evt , this.$refs.pop_move , this.startData ) ; 
            } , 
            request_serial () {
                var vm = this;
        		if ( check_input_null( vm , '序列號不能為空' , 'serial' ) ) return;
                vm.hideSerial();
            	postRequest( {
            		vm : vm,
            		params : {} , 
            		service : '',
            		successCallBack : function ( resp ) {
		            	vm.$emit('requestSerial',vm.serial) ; 
		            	vm.serial = '';
            		},
            		failureCallback : function ( resp ) { }
            	} );
            }
        } ,

    }
</script>



