<template>
    <section class="main-section" id="order-add">
        <div class="content-section">
            <form class="msg">
                <div class="flex">
                    <span class="u-w-5 ui-title">訂單類型</span>
                    <label> 
                        <select @change="orderTypeChange" v-model="orderType" class="u-w-1"> 
                            <option value="-1">請選擇訂單類型</option>
                          	<template v-for="item in orderTypes">
                        		<option v-if="item.value == 'PSC' || item.value == 'CTR' " v-bind:value="item.value">{{item.name}}</option>
                        		<option v-else v-bind:value="item.value" disabled>{{item.name}}</option>
                        	<template>
                        </select>
                    </label>
                    <label class="p-r-1 u-w-7 ui-title">
                        <i v-bind:class="{'common-checkbox-cbg':status13P.choose,'common-checkbox-noedit':status13P.edit}" class="common-checkbox"></i>
                        <span class="discount-percentage">13%優惠</span>
                    </label>
                    <span @click="isShow_Description" class="ui-w-50 ui-stress ui-small discount-description" v-text="distributor.ordererDescription"></span>
                </div>
                <div class="flex">
                    <span class="u-w-5 ui-title" >付款人</span>
                    <label>
                        <input ref="payer" @input="inputPayer" v-bind:maxlength="common_configure.distributor_len" type="tel" v-model="distributor.payer"  class="u-w-1" />
                    </label>
                    <label class="p-r-1 u-w-8">
                        <span v-show="isShow.isShowCheckSave">
                            <i @click="isChooseCheckSave=!isChooseCheckSave" v-bind:class="{'common-checkbox-cbg':isChooseCheckSave}" class="common-checkbox"></i>
                            <span class="v-a ui-blue ui-small">&nbsp暫存</span>
                        </span>
                    </label>
                    <span class="ui-w-p ui-blue ui-small">姓名</span>
                    <span class="u-w-4" v-text="distributor.payerInfo"></span>
                </div>
                <div class="flex">
                    <span class="u-w-5 ui-title">訂貨人</span>
                    <label>
                    	<input ref="orderer" @input="inputOrderer" v-bind:maxlength="common_configure.distributor_len" type="tel" v-model="distributor.orderer" class="u-w-1" />
                    </label>
                    <label class="u-w-8"></label>
                    <span class="ui-w-p ui-blue ui-small">姓名</span>
                    <span class="u-w-4" v-text="distributor.ordererInfo"></span>
                </div>
                <div class="flex" v-show="isShow.isShowConsignee">
                    <span class="u-w-5 ui-title">收貨人</span>
                    <label>
                    	<input ref="consignee" @input="inputConsignee" v-bind:maxlength="common_configure.distributor_len" type="tel" v-model="distributor.consignee" class="u-w-1" />
                    </label>
                    <label class="u-w-8"></label>
                    <span class="ui-w-p ui-blue ui-small">姓名</span>
                    <span class="u-w-4"  v-text="distributor.consigneeInfo"></span>
                </div>
                <div class="flex"  v-show="isShow.isShowConsignee">
                    <span class="u-w-5 ui-title">收貨地址</span>
                    <label class="ui-w-90">
                    	<input type="text" v-model="distributor.consigneeAddress" class="ui-w-100" readonly="readonly" />
                    </label>
                </div>
            </form>
            <form class="ui-block form-product">
                <div class="flex-div">
	                <div>
						<span class="ui-title">產品編號</span>
						<input @blur="itemNumberBlur" v-model="this_itemNumber" type="tel" class="ui-w-70" />
	                </div>
					<div>
						<span class="ui-w-5 ui-title">數量</span>
						<input @blur="propuctNumberBlur" type="tel" class="ui-w-80"  v-model="this_propuctNumber" >
					</div>
					<div>
	                    <a @click="isShow.isShowPoint=(new Date()).valueOf()" class="u-w-7 common-btn" id="credit-list-btn">購物積點</a>
					</div>
                </div>
                <div class="ui-table-scroll">
                    <div class="ui-t-h">
                        <div class="ui-w-5"><span></span></div>
                        <div class="ui-w-20"><span>產品編號</span></div>
                        <div class="ui-w-30"><span>產品名稱</span></div>
                        <div class="ui-w-10"><span>單價</span></div>
                        <div class="ui-w-10"><span>數量</span></div>
                        <div class="ui-w-10"><span>金額</span></div>
                        <div class="ui-w-15">
                            <span @click="isShow.isShowSummaryInfo=(new Date()).valueOf()" class="ui-title common-btn">總價</span>
                        </div>
                    </div>
                    <div class="ui-w-85 ui-t-m list-bg">
                        <template v-for="(queryItem , index ) of queryItems">
				            <div class="ui-t-r" v-bind:data-index="index" 
                                v-bind:class="{'ui-b-g':queryItem.choose}" 
                                @touchstart="productTouchstart(index, $event)" 
                                @touchmove="productTouchmove (index, $event)" 
                                @touchend="productTouchend(index, $event)"  
                                @touchcancel="productTouchcancel(index, $event)" >
				                <div></div>
				                <div>{{queryItem.itemNumber}}</div>
				                <div>{{queryItem.itemName}}</div>
				                <div>{{queryItem.originalPrice}}</div>
				                <div>
				                	<input @blur="orderQuantityBlur(index)" type="tel" class="ui-w-75 t-r" v-bind:value="queryItem.orderQuantity">
				                </div>
				                <div>{{queryItem.dp}}</div>
				            </div>
				        </template>
                    </div>
                    <div class="ui-blue total-info">
                        <div class="ui-w-80 ui-title total">{{orderSummaryInfo.totalAmount}}</div>
                        <div class="ui-title">總數量<span>{{orderSummaryInfo.totalQty}}</span></div>
                        <div>13%折扣金額<span>{{orderSummaryInfo.preferentialValue}}</span></div>
                        <div>訂單積分額<span>{{orderSummaryInfo.totalPV}}</span></div>
                    </div>
                </div>
            </form>
        </div>
        <div class="content-footer">
            <ul>
                <li><a @click="f1">返回</a></li>
                <li><a @click="f2">畫面<br/>清除</a></li>
                <li><a @click="f3">訂單<br/>查詢</a></li>
                <li><a @click="f4">產品抵用券<br/>查詢</a></li>
                <li><a @click="f5">客戶<br/>查詢</a></li>
                <li><a @click="f6">續約/<br/>折價券</a></li>
                <li><a @click="f7">產品<br/>明細</a></li>
                <li><a @click="f8">確認<br/>付款</a></li>
                <li><a @click="f9">送貨<br/>地址</a></li>
                <li><a @click="f10">產品<br/>序號</a></li>
                <li><a @click="f11">暫存<br/>訂單</a></li>
                <li><a @click="f12">還原<br/>訂單</a></li>
                <li><a @click="f13">取消<br/>手續費</a></li>
            </ul>
        </div>
		<v-popup :points="points" :isShowPoint="isShow.isShowPoint"></v-popup>
        <v-summaryInfo :orderSummaryInfo="orderSummaryInfo" :isShowSummaryInfo="isShow.isShowSummaryInfo"></v-summaryInfo>
        <v-description :description="distributor.ordererAllDescription" :isShowDescription="isShow.isShowDescription"></v-description>
        <!-- 子组件给父组件通讯，加传参数 -->
		<v-serial :serialMsg="{}" :isShowSerial="isShow.isShowSerial" v-on:requestSerial="requestSerialCallback" ></v-serial>
    </section>
</template>
<script>
    import { mapGetters } from 'vuex'
    require("../../assets/sass/order_main.sass")
    import popup_point from '../../components/order-components/popup-point'
    import popup_serial from '../../components/order-components/popup-serial'
    import popup_description from '../../components/order-components/popup-description'
    import popup_summaryInfo from '../../components/order-components/popup-summaryInfo'
	import { check_input_null , check_input_number,  postRequest } from '../../vuex/utils.js'
	export default {
		computed: {
			...mapGetters([
				'tip_msg',
				'common_configure',
				'usreInfo'
			])
		},
		data () {
			return {
                orderTypes : [],
                this_itemNumber : '' ,          // 当前輸入的產品
                this_propuctNumber : 1 ,        // 默认的产品数量
                orderType : '-1',               // 当前订单类型
                status13P : {                   // 优惠13的信息
                    choose: false , 
                    edit : false 
                } , 
                isChooseCheckSave : false ,     // 暂存勾选框 是否选择的 变量
                isShow : {
                    isShowCheckSave : false ,   // 是否显示暂存勾选框
                    isShowConsignee : false ,   //是否显示收货人与收货人地址信息
                    isShowPoint : false ,       // s是否显示购物绩点
                    isShowSummaryInfo : false , // 是否显示价格信息
                    isShowSerial : false ,      // 是否显示产品序列号
                    isShowDescription : false , // 是否显示人员的信息
                } , 
                distributor : {
                	payer : '' , 
                	payerInfo : '' , 
                	orderer : '' , 
                	ordererInfo : '' , 
                    ordererDescription : '' ,
                    ordererAllDescription : '' ,
                	consignee : '' ,
                	consigneeInfo : '' ,
                	consigneeAddress : '' ,
                } , 
                points : {
                	birthdayMsgStr : '' ,
					remainPoints : 0 ,
					gainPoints : 0 ,
					exchangePointsHistory : 0 ,
					totalPoints : 0 ,
					promotionPoints : 0 
                } ,
                orderSummaryInfo : {
					totalQty : 0 ,
					totalBV : 0 ,
					totalPV : 0 ,
					deliveryCharge : 0 ,
					promotionTotal : 0 ,
					promotionBV : 0 ,
					promotionPV : 0 ,
                	totalAmount : 0 ,
                	totalQty : 0 ,
                	preferentialValue : 0 ,
                	totalPV : 0 
                } ,
                queryItems : [ 
                    {
                        "choose" : false,
                        "startX" : "" , 
                        "resultCode": "0",
                        "itemNumber": "WHU5147TW1111111 ",
                        "orderQuantity": 2,
                        "originalPrice": 27395,
                        "dp": 54790,
                        "bv": 52180,
                        "itemName": "益之源淨水器分流器組一次付清",
                        "pv": 0,
                        "priceItemNumber": "9420",
                        "itemStatus": "M",
                        "warrantyFlag": 0
                    },{
                        "choose" : false,
                        "startX" : "" , 
                        "resultCode": "0",
                        "itemNumber": "WHU5147TW22222222 ",
                        "orderQuantity": 2,
                        "originalPrice": 27395,
                        "dp": 54790,
                        "bv": 52180,
                        "itemName": "益之源淨水器分流器組一次付清",
                        "pv": 0,
                        "priceItemNumber": "9420",
                        "itemStatus": "M",
                        "warrantyFlag": 0
                    },{
                        "choose" : false,
                        "startX" : "" , 
                        "resultCode": "0",
                        "itemNumber": "WHU5147TW3333333 ",
                        "orderQuantity": 2,
                        "originalPrice": 27395,
                        "dp": 54790,
                        "bv": 52180,
                        "itemName": "益之源淨水器分流器組一次付清",
                        "pv": 0,
                        "priceItemNumber": "9420",
                        "itemStatus": "M",
                        "warrantyFlag": 0
                    },{
                        "choose" : false,
                        "startX" : "" , 
                        "resultCode": "0",
                        "itemNumber": "WHU5147TW 4444444",
                        "orderQuantity": 2,
                        "originalPrice": 27395,
                        "dp": 54790,
                        "bv": 52180,
                        "itemName": "益之源淨水器分流器組一次付清",
                        "pv": 0,
                        "priceItemNumber": "9420",
                        "itemStatus": "M",
                        "warrantyFlag": 0
                    }
				] ,
                inputBarcodeMsg : {
                    inputBarcodeNumber : { } ,//记录需要输入产品序号的产品的数量，格式：{ 产品1:数量1,产品2:数量2,}
                    inputBarcode : { }        //记录已经输入的产品序号，格式：{产品1:[序号1，序号2...],产品2:[序号1，序号2...], ...}
                }

			}
		} , 
        activated () {

        } , 
        deactivated () {
        } , 
        created : function(){
            var vm = this;
            postRequest( {
                vm : vm,
                params : {} , 
                service : 'queryOrderType',
                successCallBack : function ( resp ) {
                    vm.orderTypes = resp.orderType;
                },
                failureCallback : function ( resp ) {}
            } );
        },
        components : {
        	'v-popup' : popup_point ,
            'v-summaryInfo' : popup_summaryInfo , 
            'v-description' : popup_description ,
        	'v-serial' : popup_serial 
        },
		methods : {
            orderTypeChange () {
                this.init_page();
                this.isChooseCheckSave = false ;
                this.isShow.isShowCheckSave = ( this.orderType === 'PSC' );
                this.isShow.isShowConsignee = ( this.orderType !== 'PSC' &&  this.orderType !== '-1');
            } , 
            init_page () {
                this.init_orderSummaryInfo();
                this.init_distributor();
                this.init_queryItems();
                this.init_points();
                this.init_input_product();
            },
            init_orderSummaryInfo () {
                this.orderSummaryInfo = {
                    totalQty : 0 ,
                    totalBV : 0 ,
                    totalPV : 0 ,
                    deliveryCharge : 0 ,
                    promotionTotal : 0 ,
                    promotionBV : 0 ,
                    promotionPV : 0 ,
                    totalAmount : 0 ,
                    totalQty : 0 ,
                    preferentialValue : 0 ,
                    totalPV : 0 
                };
            },
            init_input_product () {
                this.this_itemNumber = '' ;
                this.this_propuctNumber = 1 ;
            },
            init_distributor ( distributor ) {
                if ( distributor instanceof Object ) {
                    this.distributor = Object.assign( this.distributor , distributor );
                } else {
                    this.distributor = {
                        payer : '' , 
                        payerInfo : '' , 
                        orderer : '' , 
                        ordererInfo : '' , 
                        consignee : '' ,
                        consigneeInfo : '' ,
                        consigneeAddress : ''
                    } ; 
                }
            }, 
            init_queryItems () {
                this.queryItems = [
                ] ;
            },           
            init_points () {
                this.points = {
                    birthdayMsgStr : '' ,
                    remainPoints : 0 ,
                    gainPoints : 0 ,
                    exchangePointsHistory : 0 ,
                    totalPoints : 0 ,
                    promotionPoints : 0 
                } ; 
            },
            init_isShow () {
                this.isShow = {
                    isShowCheckSave : false , // 是否显示暂存勾选框
                    isShowConsignee : false , //是否显示收货人与收货人地址信息
                    isShowPoint : false , 
                    isShowSummaryInfo : false ,
                    isShowSerial : false , 
                    isShowDescription : false ,
                } ; 
            },
            request_QueryPointsOrderList4Mob ( callback ) {
                var vm = this;
                postRequest( {
                    vm : vm,
                    params : {} , 
                    service : 'QueryPointsOrderList4Mob',
                    successCallBack : function ( resp ) {
                        var orderSummaryInfo = resp.orderSummaryInfo ;
                        Object.assign( vm.points ,  {
                            remainPoints : orderSummaryInfo.remainPoints ,
                            gainPoints : orderSummaryInfo.gainPoints ,
                            exchangePointsHistory : orderSummaryInfo.exchangePointsHistory ,
                            totalPoints : orderSummaryInfo.totalPoints ,
                            promotionPoints : orderSummaryInfo.promotionPoints 
                        });
                        vm.orderSummaryInfo = {
                            totalQty : orderSummaryInfo.totalQty ,
                            totalBV : orderSummaryInfo.totalBV ,
                            totalPV : orderSummaryInfo.totalPV ,
                            deliveryCharge : orderSummaryInfo.deliveryCharge ,
                            promotionTotal : orderSummaryInfo.promotionTotal ,
                            promotionBV : orderSummaryInfo.promotionBV ,
                            promotionPV : orderSummaryInfo.promotionPV ,
                            totalAmount : orderSummaryInfo.totalAmount ,
                            totalQty : orderSummaryInfo.totalQty ,
                            preferentialValue : orderSummaryInfo.preferentialValue ,
                            totalPV : orderSummaryInfo.totalPV 
                        }; 
                        vm.orderSummaryInfo.preferentialValue = resp.status13P.preferentialValue;
                        if ( typeof callback == 'function' ) {
                            callback.call(vm);
                        }
                    },
                    failureCallback : function ( resp ) { }
                } );
            },
            retrieveDistributor_callback_consignee(resp) {
            },
            retrieveDistributor_callback_orderer(resp) {
                var dis = "";
                var dis_show = "";
                if(resp.pref13MsgStr)
                    dis = resp.pref13MsgStr+"<br/>";
                if(resp.renewalPrefMsgStr)
                    dis += resp.renewalPrefMsgStr+"<br/>";
                if(resp.renewalMsgStr)
                    dis += resp.renewalMsgStr+"<br/>";
                if(resp.epdCouponValueMsgStr)
                    dis += resp.epdCouponValueMsgStr+"<br/>";
                if(resp.pointCashMsgStr)
                    dis += resp.pointCashMsgStr+"<br/>";
                if(resp.newPrefMsgStrA)
                    dis += resp.newPrefMsgStrA+"<br/>";
                if(resp.newPrefMsgStrB)
                    dis += resp.newPrefMsgStrB+"<br/>";
                if(resp.newPrefCouponMsgStrA)
                    dis += resp.newPrefCouponMsgStrA+"<br/>";
                if(resp.newPrefCouponMsgStrB)
                    dis += resp.newPrefCouponMsgStrB+"<br/>";
                if(resp.totalCouponValueMsgStr)
                    dis += resp.totalCouponValueMsgStr+"<br/>";
                if ( (dis.match(/<br\/>/g) &&  dis.match(/<br\/>/g).length > 1) || (dis.split('<br/>')[0]  && dis.split('<br/>')[0].length > 35)) {
                    dis_show = dis.split('<br/>')[0].substring(0,34) + '............';
                } else {
                    dis_show = dis;
                }
                this.distributor.ordererDescription = dis_show ; 
                this.distributor.ordererAllDescription = dis ; 
                this.points.birthdayMsgStr  = resp.birthdayMsgStr || '' ;
                this.request_QueryPointsOrderList4Mob();
            },
            retrieveDistributor_callback_payer(resp) {
            },
            request_distributor( num , type ) {
            	var vm = this;
	            postRequest( {
	                vm : vm,
	                params : {} , 
	                service : 'retrieveDistributor',
	                successCallBack : function ( resp ) {
	                	vm.distributor[type+'Info'] = resp.disPlayDistNameInfo;
	                	if ( type === 'orderer' ) {
                            vm.retrieveDistributor_callback_orderer(resp) ;
	                	} else if ( type === 'payer' ) {
                            vm.retrieveDistributor_callback_payer(resp) ;
                        } else if ( type === 'consignee' ) {
                            vm.retrieveDistributor_callback_consignee(resp) ;
                        }
	                },
	                failureCallback : function ( resp ) { }
	            } );
            },
            check_ordertype () {
                if ( this.orderType == '-1' ) {
                    this.$store.commit('UPDATE_PROMPT', this.tip_msg.orderTips.orderTypeIsChoose);
                    return true ; 
                } 
                return false ; 
            } , 
            check_distributor( msg, type ) {
                if ( this.check_ordertype() ) return ; 
            	var vm = this;
            	var val = vm.distributor[type];
            	if ( check_input_number(vm , msg , type , this.distributor ) ) return ; 
				if ( val.trim().length === 7 ) {
					vm.request_distributor( val.trim() , type );
				}
            },
            inputPayer () {
            	this.check_distributor( '付款人' , 'payer' );
            } , 
			inputOrderer () {
            	this.check_distributor( '訂貨人' , 'orderer' );
            } , 
			inputConsignee () {
            	this.check_distributor( '收貨人' , 'consignee' );
            } , 
            request_itemNumber ( itemNumber ) {
            	var vm = this;
            	function _format_repeat (obj) {
	            	var repeat = false ; 
            		vm.queryItems.forEach( function( item , index ) {
            			if ( item.priceItemNumber == obj.priceItemNumber ) {
            				vm.queryItems.splice(index , 1 , obj ) ; 
            				repeat = true ; 
            			}
            		});
            		if ( !repeat ) {
            			vm.queryItems.push( obj );
            		}
            	}
	            postRequest( {
	                vm : vm,
	                params : {} , 
	                service : 'queryItem',
	                successCallBack : function ( resp ) {
	                	_format_repeat (resp) ; 
	                },
	                failureCallback : function ( resp ) { }
	            } );

            } , 
            itemNumberBlur () {
            	if ( this.this_itemNumber.trim() === '' ) return ;
            	this.request_itemNumber ( this.this_itemNumber.trim() ) ;
            },
            isShow_Description () {
                if ( this.distributor.ordererDescription.indexOf('............') !== -1 ) {
                    this.isShow.isShowDescription = (new Date()).valueOf() ; 
                }
            } , 
            propuctNumberBlur () {

            },
            orderQuantityBlur ( index ) {
            } , 

            /**************************序列号*****************************/
            requestSerialCallback (serial) {
                this.isShow.isShowSerial = new Date().valueOf();
            } , 
            /**************************移动触摸屏删除产品的方法*****************************/
            productTouchstart ( index , evt ) { 
                this.queryItems[index].startX = Number(evt.touches[0].pageX) ; 
                this.queryItems[index].choose = true ; 
            } , 
            productTouchmove ( index , evt ) {
                var touches = evt.touches[0];
                var m_x   = touches.pageX;
                if ( this.queryItems[index] && this.queryItems[index].startX ) {
                    var startX =  +this.queryItems[index].startX ;
                    var change_x = startX - m_x;
                    if( Math.abs(change_x) > 100 ){ 
                        this.queryItems.splice( index , 1 ) ;
                        this.request_QueryPointsOrderList4Mob();
                        return;
                    }
                }
            } ,
            productTouchend ( index , evt ) {
                if ( this.queryItems[index] ) { 
                    this.queryItems[index].choose = false ; 
                }
            } ,
            productTouchcancel ( index , evt ) {
                if ( this.queryItems[index] ) { 
                    this.queryItems[index].choose = false ; 
                }
            } , 
            f1 ( ) { 
                this.$router.push('meun_order');
            },
            f2 ( ) { 

            },
            f3 ( ) { 

            },
            f4 ( ) { 

            },
            f5 ( ) { 

            },
            f6 ( ) { 

            },
            f7 ( ) { 

            },
            f8 ( ) { 

            },
            f9 ( ) { 

            },
            f10 ( ) { 

            },
            f11 ( ) { 

            },
            f12 ( ) { 

            },
            f13 ( ) { 

            }
		}
	}
</script>



