<template>
    <transition name="animation-prompt">
        <div v-show="prompt.isShow" class="common-ui-pop_move">
            <div v-on:touchstart="popTouchstart($event)" v-on:touchmove="popTouchmove($event)" ref="pop_move" class="common-ui-pop-content_move">
                <div style="word-break:break-all">{{prompt.msg}}</div>
                <div class="common-ui-pop-btn">
                    <a class="common-btn" @click="confirm">{{prompt.button.confirm}}</a>
                    <a class="common-btn" v-if="prompt.buttonNum === 2 " @click="cancel" >{{prompt.button.cancel}}</a>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex'
    import { move_pop_touchstart , move_pop_init_startData , move_pop_touchmove } from '../vuex/utils.js'
    export default {
        data () {
            return {
                startData : {
                    initTop : 0,
                    initLeft : 0,
                    startTop : 0 , 
                    startLeft : 0 , 
                    startX : 0 , 
                    startY : 0 
                }
            }
        } , 
        computed : mapGetters({
            prompt:'prompt' 
        }) , 
        methods : {
            confirm () {
                var confirmCallBack = this.prompt.confirmCallBack ; 
                if ( typeof confirmCallBack === 'function' ) {
                    confirmCallBack.call(this);
                }
                this.init_move_coordinate();
                this.$store.commit('UPDATE_PROMPT' , {
                    isShow : false,
                    msg:'',
                    buttonNum:1,
                    button: {
                        confirm : '確定' ,
                        cancel : '取消' 
                    } , 
                    confirmCallBack : function () {} ,
                    cancelCallBack : function () {} 
                } ) ; 
            } , 
            cancel () {
                var cancelCallBack = this.prompt.cancelCallBack ; 
                if ( typeof cancelCallBack === 'function' ) {
                    cancelCallBack.call(this);
                }
                this.init_move_coordinate();
                this.$store.commit('UPDATE_PROMPT' ,{
                    isShow : false,
                    msg:'',
                    buttonNum:1,
                    button: {
                        confirm : '確定' ,
                        cancel : '取消' 
                    } , 
                    confirmCallBack : function () {} ,
                    cancelCallBack : function () {} 
                } ) ; 
            } , 
            init_move_coordinate ( evt ) {
                move_pop_init_startData(  this.$refs.pop_move , this.startData) ; 
            } , 
            popTouchstart ( evt ) {
                move_pop_touchstart( evt , this.$refs.pop_move , this.startData ); 
            } , 
            popTouchmove ( evt ) {
                move_pop_touchmove(evt , this.$refs.pop_move , this.startData ) ; 
            }
        }
    }
</script>
<style>
    .animation-prompt-enter-active, .animation-prompt-leave-active {transition: transform .5s }
    .animation-prompt-enter, .animation-prompt-leave-active {
        transform: scale(0,0) rotate(240deg); 
    }
</style>


