<template>
    <header class="main-header">
        <div @click="isShowMenuFun()" v-if="common_menu"  class="common-menu">
            <i class="sprite sprite-icon-menu"></i>
        </div>
        <h1 class="ui-large">
            <i class="sprite sprite-logo"></i>
            <span>店鋪銷售系統 POS</span>
        </h1>
    </header>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        computed:mapGetters({
            common_menu : 'common_menu'
        }),
        methods:{
            isShowMenuFun () {
                this.$store.commit('UPDATE_ISSHOWMENU')
            }
        }
    }
</script>


