<template>
	<div class="content-section" id="home">
        <v-meunUserInfo></v-meunUserInfo>
		<div class="menuButList">
			<div class="but-column">
				<div class="but-warp">
					<div @click="goPageSubMeun('meun_join')" class="main-but">
						<i class="sprite sprite-icon-add-white"></i>
						<span class="homeMenu-font-style">加入</span>
					</div>
				</div>
				<div class="but-warp">
					<div @click="goPageSubMeun('meun_order')" class="main-but">
						<i class="sprite sprite-icon-order-white"></i>
						<span class="homeMenu-font-style">訂貨</span>
					</div>
				</div>
				<div class="but-warp">
					<div @click="goPageSubMeun('meun_renew')" class="main-but">
						<i class="sprite sprite-icon-renew-white"></i>
						<span class="homeMenu-font-style">續約</span>
					</div>
				</div>
			</div>
			<div class="but-column">
				<div class="but-warp">
					<div @click="goPageSubMeun('meun_query')" class="main-but">
						<i class="sprite sprite-icon-query-white"></i>
						<span class="homeMenu-font-style">查詢</span>
					</div>
				</div>
				<div class="but-warp">
					<div @click="goPageSubMeun()" class="main-but but-color">
						<i class="sprite sprite-icon-refund-white"></i>
						<span class="homeMenu-font-style">退貨</span>
					</div>
				</div>
				<div class="but-warp">
					<div @click="goPageSubMeun()" class="main-but but-color">
						<i class="sprite sprite-icon-repair-white"></i>
						<span class="homeMenu-font-style">維修</span>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	require("../../assets/sass/meun_main.sass")
    import { mapGetters } from 'vuex'
    import meunUserInfo from '../../components/meunUserInfo'
	export default {
		computed: {
			...mapGetters([
				'usreInfo'
			])
		},
		data () {
			return {
				staffName:'',
				shopName:'',
				posCode:''
			}
		} , 
        created:function(){
			this.shopName = this.usreInfo.shopName ;
			this.posCode = this.usreInfo.posCode ; 
			this.staffName = this.usreInfo.staffName ; 
        },
        components:{
            'v-meunUserInfo' : meunUserInfo 
        },
		methods : {
			goPageSubMeun ( subName) {
				if ( subName ) {
					this.$router.push(subName);
				}
			}
		}
	}
</script>


