<template>
    <div class="flex-body">
        <v-header></v-header>
        <v-mainTitle></v-mainTitle>
        <v-mainMenu :isShowMenu="isShowMenu"></v-mainMenu>
        <v-loading></v-loading>
        <v-loading></v-loading>
        <v-prompt></v-prompt>
        <v-prompts></v-prompts>
        <transition :name="transitionName"> 
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </transition>
    </div>
</template>
<script>
    // require("src/assets/common.css")
    import {mapGetters} from 'vuex'
    import mainTitle from './components/mainTitle'
    import mainMenu from './components/mainMenu'
    import loading from './components/loading'
    import header from './components/header'
    import prompt from './components/prompt'
    import prompts from './components/prompts'
    export default {
        data(){
            return {
                transitionName:'slide-left'
            }
        },
        //实例已经创建完成之后被调用。在这一步，实例已完成以下的配置：数据观测(data observer)，属性和方法的运算， watch/event 事件回调。然而，挂载阶段还没开始，$el 属性目前不可见
        created:function(){
            if (this.$route.name == undefined) {
                return this.$router.push('login')
            }  
            if (this.$route.name == 'login') {
                this.$store.commit('UPDATE_TITLE','login')
            }
        },
        //watch： 键是需要观察的表达式，值是对应回调函数。值也可以是方法名，或者包含选项的对象。Vue 实例将会在实例化时调用 $watch()，遍历 watch 对象的每一个属性
        watch:{
            '$route' (to,from){
                // alert(from.name)
                this.$store.commit('UPDATE_STATE' , { 
                    title : to.name,
                    common_menu : true,
                    isShowMenu:false,
                    loading : false 
                });
            }
        },
        computed: {
            ...mapGetters([
                'loading' ,
                'common_menu',
                'isShowMenu'
            ])
        },
        components:{
            'v-mainMenu' : mainMenu , 
            'v-mainTitle' : mainTitle,
            'v-header' : header,
            'v-loading' : loading,
            'v-prompt' : prompt, 
            'v-prompts' : prompts
        }
    }
</script>
<style>
    /* 路由1跳转路由2，路由1页面的  动画时间 */
    .slide-left-leave-active {transition: all .2s ; }
    /* 路由1页面的 与陆游2页面替换过程，陆游1页面的动画 */
    .slide-left-leave-active{ transform: translateX(-700px); opacity: 0;  }
    /* 路由1跳转路由2，路由2页面的  动画时间 */
    .slide-left-enter-active {transition: all .3s linear .2s; }
    /* 路由2页面的  动画开始进来的效果 */
    .slide-left-enter { transform: translateX(700px); opacity: 0; }
</style>


