<template>
	<div class="content-section" id="login">
		<form>
			<h3>登錄</h3>
			<div>
				<span class="span-left">地區代碼&nbsp:</span>
				<input v-model="regionCode" type="tel" readonly="readonly" />
			</div>
			<div>
				<span class="span-left">中心編號&nbsp:</span>
				<input ref="shopCode" v-model="shopCode" type="tel"/>
			</div>
			<div>
				<span class="span-left">員工編號&nbsp:</span>
				<input ref="staffNumber" v-model="staffNumber" type="text"/>
			</div>								
			<div class="common-ui-pop-btn btn-margin">
				<a  @click="login" class="common-btn">登&nbsp&nbsp錄</a>
            </div>
		</form>
	</div>
</template>
<script>
	require("../../assets/sass/login.sass")
    import { mapGetters } from 'vuex'
	import {check_input_null , postRequest } from '../../vuex/utils.js'
	export default {
		computed: {
			...mapGetters([
				'tip_msg'
			])
		},
		data () {
			return {
				shopCode : "" , 
				staffNumber : "" ,
				regionCode : "130"
			}
		} , 
		created:function(){
			 // DOM 还没有更新
			this.$nextTick(function(){
			// DOM 现在更新了
			})
        },
        activated () {
            // <keep-alive> 虽然全局设置了 <keep-alive> 属性，不过当其他页面返回到该页面，该方法还是执行
			this.$store.commit('UPDATE_COMMON_MENU' , true );
        } , 
        deactivated () {
        },
        components  :{
        },
		methods : {
            login () { 
            	/*多个弹出框*/
				// var vm = this;
				// vm.$store.commit('UPDATE_PROMPTS' , { 
				// 	msgs : [ 
				// 		{msg : '1' , button : {confirm : '確定', cancel : '取消', } } , 
				// 		{msg : '2' , button : {confirm : '確定'} } , 
				// 		{msg : '3' , button : {confirm : '確定', cancel : '取消', } }
				// 	] ,
				// 	confirmCallBack : function () {
				// 		alert('confirmCallBack')
				// 	} , 
				// 	cancelCallBack : function () {
				// 		alert('cancelCallBack')
				// 	}
				// } ) ; 
				// return;
            	var vm = this;
        		if ( check_input_null( vm , vm.tip_msg.publicTips.err_15 , 'shopCode' ) ) return;
        		if ( check_input_null( vm , vm.tip_msg.publicTips.err_16 , 'staffNumber' ) ) return;
            	postRequest( {
            		vm : vm,
            		params : {} , 
            		service : 'checkCenterStatus',
            		successCallBack : function ( resp ) {
						vm.$store.commit('USER_INFO' , resp ); 
						var gopage_router;
						switch( +resp.loginReturnFlag ){
							case 0 : gopage_router = 'login' ; break;
							case 1 : gopage_router = 'login_pos' ; break;
							case 2 : gopage_router = 'login_pos_open' ; break;
							case 3 : gopage_router = 'login_center_open' ; break;
						}
						vm.$router.push(gopage_router);
						// vm.$router.push('meun_main');
            		},
            		failureCallback : function ( resp ) { 
						vm.$store.commit('UPDATE_LOADING' , false );
            		}
            	} );
			}
		}
	}
</script>



