<template>
    <transition name="animation-menu">
        <div v-show="isShowMenu" class="main-menu">
            <p class="ui-title">主功能目錄</p>
            <ul>
                <li>
                    <a @click="goPage('meun_join')" class="add">
                        <i class="sprite sprite-icon-add-small"></i>
                        <span>加入</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('meun_order')" class="order">
                        <i class="sprite sprite-icon-order-small"></i>
                        <span>訂貨</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('meun_renew')" class="renew">
                        <i class="sprite sprite-icon-renew-small"></i>
                        <span>續約</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('meun_query')" class="query">
                        <i class="sprite sprite-icon-query-small"></i>
                        <span>查詢</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('')" class="refund">
                        <i class="sprite sprite-icon-refund-gray-small"></i>
                        <span>退貨</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('')" class="repair">
                        <i class="sprite sprite-icon-repair-gray-small"></i>
                        <span>維修</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('login_modify_pwd')" class="doChangePassword">
                        <i class="sprite sprite-icon-modify-password-small"></i>
                        <span>修改密碼</span>
                    </a>
                </li>
                <li>
                    <a @click="goPage('login_leave')" class="exit">
                        <i class="icon icon-exit"></i>
                        <span>暫時離開</span>
                    </a>
                </li>
                <li>
                    <div class="main-aside-logout">
                        <div><span class="staffName"></span>您好</div>
                        <div class="ui-small">營 業 日 期</div>
                        <div class="ui-small saleDate"></div>
                        <a @click="goPage('login_graduation')" class="login-out">登&nbsp;出</a>
                    </div>
                </li>
            </ul>
        </div>
    </transition>
</template>
<script>
    export default {
        props: {
            isShowMenu: {
                type: Boolean
            }
        },
        data() {
            return  {
            }
        } ,
        methods : {
            goPage ( subName) {
                if ( subName ) {
                    this.$router.push(subName);
                }
            }
        }
    }
</script>
<style>
    .animation-menu-enter-active, .animation-menu-leave-active {transition: left 0.5s }
    .animation-menu-enter, .animation-menu-leave-active {  left: -10rem; }
</style>


