<template>
    <transition name="animation-tips">
        <div v-show="showPoint" class="common-ui-pop_move" data-pop >
            <div v-on:touchstart="popTouchstart($event)" v-on:touchmove="popTouchmove($event)" ref="pop_move" class="common-ui-pop-content_move">
                <table>
                    <caption id="pointsMsg" class="ui-stress">{{points.birthdayMsgStr}}</caption>
                    <tbody>
                        <tr>
                            <td>原有點數</td>
                            <td>{{points.remainPoints}}</td>
                        </tr>
                        <tr>
                            <td>訂單點數</td>
                            <td>{{points.gainPoints}}</td>
                        </tr>
                        <tr>
                            <td>兌換點數</td>
                            <td>{{points.exchangePointsHistory}}</td>
                        </tr>
                        <tr>
                            <td>剩餘點數</td>
                            <td>{{points.totalPoints}}</td>
                        </tr>
                        <tr>
                            <td>加碼點數</td>
                            <td>{{points.promotionPoints}}</td>
                        </tr>
                    </tbody>
                </table>
                <div class="common-ui-pop-btn">
                    <a @click="hidePoint" class="common-btn">確認</a>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex'
    import { move_pop_touchstart , move_pop_init_startData , move_pop_touchmove } from '../../vuex/utils.js'
    export default {
        data () {
            return {
                startData : {
                    initTop : 0,
                    initLeft : 0,
                    startTop : 0 , 
                    startLeft : 0 , 
                    startX : 0 , 
                    startY : 0 
                } ,
                showPoint : false
            }
        } , 
        props : {
            points: {
                type: Object
            },
            isShowPoint : {
            }
        },
        watch : {
            'isShowPoint' (to , from) { 
                if ( to == false ) {
                    this.showPoint = false;
                } else { 
                    this.showPoint = true;
                }
            }
        } , 
        methods:{
            hidePoint () {
                this.init_move_coordinate() ;
                this.showPoint = false ; 
            } , 
            init_move_coordinate ( ) {
                var vm = this;
                setTimeout(function(){
                    move_pop_init_startData(  vm.$refs.pop_move , vm.startData) ; 
                },500);
            } , 
            popTouchstart ( evt ) {
                move_pop_touchstart( evt , this.$refs.pop_move , this.startData ); 
            } , 
            popTouchmove ( evt ) {
                move_pop_touchmove( evt , this.$refs.pop_move , this.startData ) ; 
            }
        } ,

    }
</script>



