<template>
	<div class="content-section" id="login">
		<form>
			<h3>POS登錄</h3>
			<div>
				<span class='span-left'>店鋪名稱&nbsp:</span>
				<span class='span-right' v-text="usreInfo.shopName" ></span>
			</div>
			<div>
				<span class='span-left'>POS編號&nbsp:</span>
				<input type="text" v-model="posCode" readonly/>
			</div>
			<div>
				<span class='span-left'>員工編號&nbsp:</span>
				<input type="text" v-model="staffNumber" readonly/>
			</div>	
			<div>
				<span class='span-left'>營業日期&nbsp:</span>
				<input type="text" v-model="saleDate" readonly/>
			</div>
			<div>
				<span class='span-left'>密碼&nbsp:</span>
				<input v-focus="focusStatus.password" v-model="password" type="password"/>
			</div>
			<div class="common-ui-pop-btn btn-margin">
				<a  @click="back" class="common-btn">返&nbsp&nbsp回</a>
				<a  @click="login" class="common-btn">登&nbsp&nbsp錄</a>
            </div>
		</form>
	</div>
</template>
<script>
	require("../../assets/sass/login.sass")
    import { mapGetters } from 'vuex'
	import { focusValue , postRequest } from '../../vuex/utils.js'

	export default {
		computed: {
			focusValue : focusValue,
			...mapGetters([
				'tip_msg',
				'usreInfo'
			])
		},
		data () {
			return {
				shopName:'',
				posCode:'',
				staffNumber:'',
				saleDate:'',
				password:'',
				focusStatus:{
					password : '0'
				}
			}
		} , 
        activated () {
			this.$store.commit('UPDATE_COMMON_MENU' , false );
			this.password='';
        } , 
        created:function(){
			this.shopName = this.usreInfo.shopName ; 
			this.posCode = this.usreInfo.posCode ; 
			this.staffNumber = this.usreInfo.staffNumber ; 
			this.saleDate = this.usreInfo.saleDate ; 
        },
		methods : {
            login () { 
            	var vm = this;
            	if( this.password === "" ) {
            		var vm = this;
            		this.$store.commit('UPDATE_PROMPT', { 
            			msg : this.tip_msg.publicTips.err_05 , 
            			confirmCallBack : function () {
            				vm.focusStatus.password = focusValue() ; 
            			} 
            		}); 
            		return;
            	}
            	postRequest( {
            		vm : vm,
            		params : {} , 
            		service : 'logonPOS',
            		successCallBack : function ( resp ) {
						vm.$router.push('home');
            		},
            		failureCallback : function ( resp ) { 
						vm.$store.commit('UPDATE_LOADING' , false );
            		}
            	} );
			} , 
			back () {
            	this.$router.back();
			}
		}
	}
</script>
<style>
</style>


