<template>
    <transition name="animation-loading">
	    <div v-show="loading" class="common-ui-pop common-ui-loading">
	        <div class="iconf"></div>
	    </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        computed:mapGetters({
            loading : 'loading'
        })
    }
</script>
<style>
    .animation-loading-enter-active, .animation-loading-leave-active {transition: opacity 1s }
    .animation-loading-enter, .animation-loading-leave-active {  opacity: 0; }
</style>


