<template>
    <transition name="animation-description">
        <div v-show="showDescription" class="common-ui-pop_move" data-pop >
            <div v-on:touchstart="popTouchstart($event)" v-on:touchmove="popTouchmove($event)" ref="pop_move" class="warp_description">
                <span v-text="description"></span>
                <div @click="hideDescription" class="close">X</div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex'
    import { move_pop_touchstart , move_pop_init_startData , move_pop_touchmove } from '../../vuex/utils.js'
    export default {
        data () {
            return {
                startData : {
                    initTop : 0,
                    initLeft : 0,
                    startTop : 0 , 
                    startLeft : 0 , 
                    startX : 0 , 
                    startY : 0 
                } ,
                showDescription : false
            }
        } , 
        props : {
            description: {type: String },
            isShowDescription : {}
        },
        created : function() {
        	this.showDescription = this.isShowDescription;
        },
        watch : {
            'isShowDescription' (to , from) { 
                if ( to == false ) {
                    this.showDescription = false;
                } else { 
                    this.showDescription = true;
                }
            }
        } , 
        methods:{
            hideDescription () {
                this.init_move_coordinate() ;
                this.showDescription = false ; 
            } , 
            init_move_coordinate ( ) {
                var vm = this;
                setTimeout(function(){
                    move_pop_init_startData(  vm.$refs.pop_move , vm.startData) ; 
                },500);
            } , 
            popTouchstart ( evt ) {
                move_pop_touchstart( evt , this.$refs.pop_move , this.startData ); 
            } , 
            popTouchmove ( evt ) {
                move_pop_touchmove( evt , this.$refs.pop_move , this.startData , true ) ; 
            }
        } ,

    }
</script>



