<template>
	<div class="content-section" id="login">
		<form>
			<h3>修改密碼</h3> 
			<div>
				<span class='span-left'>舊密碼&nbsp:</span>
				<input ref="password" v-model="password" type="password"/>
			</div>
			<div>
				<span class='span-left'>新密碼&nbsp:</span>
				<input ref="newPassword" v-model="newPassword" type="password"/>
			</div>
			<div>
				<span class='span-left'>確認密碼&nbsp:</span>
				<input ref="confirmPassword" v-model="confirmPassword" type="password"/>
			</div>
			<div class="common-ui-pop-btn btn-margin">
				<a  @click="back" class="common-btn">返&nbsp&nbsp回</a>
				<a  @click="modify" class="common-btn">修&nbsp改</a>
            </div>
		</form>
	</div>
</template>
<script>
	require("../../assets/sass/login.sass")
    import { mapGetters } from 'vuex'
	import { check_input_null ,postRequest } from '../../vuex/utils.js'
	export default {
		computed: {
			...mapGetters([
				'tip_msg',
			])
		},
		data () {
			return {
				shopName:'',
				posCode:'',
				staffNumber:'',
				saleDate:'',
				password:'',
				confirmPassword:'',
				newPassword:''
			}
		} , 
		methods : {
            modify () { 
        		var vm = this;
        		var password = vm.password;
        		var newPassword = vm.newPassword;
        		var confirmPassword = vm.confirmPassword;
        		if ( check_input_null( vm , vm.tip_msg.publicTips.err_08 , 'password' ) ) return;
        		if ( check_input_null( vm , vm.tip_msg.publicTips.err_09 , 'newPassword' ) ) return;
        		if ( check_input_null( vm , vm.tip_msg.publicTips.err_10 , 'confirmPassword' ) ) return;
				if( newPassword.length < 6 || newPassword.length > 16 ) {
					vm.$store.commit('UPDATE_PROMPT', { msg : vm.tip_msg.publicTips.err_07 });
					return false;
				}
				if( password === newPassword ){
					vm.$store.commit('UPDATE_PROMPT', {msg : vm.tip_msg.publicTips.err_13 });
					return false;
				}
				if( newPassword != confirmPassword ){				
					vm.$store.commit('UPDATE_PROMPT', {msg : vm.tip_msg.publicTips.err_06 });
					return false;
				}
				if( newPassword.search(/[a-zA-Z]/) === -1 ){
					vm.$store.commit('UPDATE_PROMPT', {msg : vm.tip_msg.publicTips.err_11 });
					return false;
				}
            	postRequest( {
    				vm : vm,
            		params : {} , 
            		service : 'doChangePassword', 
            		successCallBack : function ( resp ) {
						vm.$router.push('meun_main');
            		},
            		failureCallback : function ( resp ) { 
						vm.$store.commit('UPDATE_LOADING' , true );
            		}
            	} );
			} , 
			back () {
            	this.$router.back();
			}
		}
	}
</script>
<style>
</style>


