<template>
	<div class="content-section" id="login">
		<form>
			<h3>POS開業</h3>
			<div>
				<span class='span-left'>店鋪名稱&nbsp:</span>
				<span class='span-right' v-text="usreInfo.shopName" ></span>
			</div>
			<div>
				<span class='span-left'>POS編號&nbsp:</span>
				<input type="text" v-model="posCode" readonly/>
			</div>
			<div>
				<span class='span-left'>營業日期&nbsp:</span>
				<input type="text" v-model="saleDate" />
			</div>

			<div>
				<span class='span-left'>員工編號&nbsp:</span>
				<input type="text" v-model="staffNumber" readonly/>
			</div>	
			<div>
				<span class='span-left'>員工姓名&nbsp:</span>
				<input type="text" v-model="staffName" readonly/>
			</div>
			<div>
				<span class='span-left'>密碼&nbsp:</span>
				<input ref="password" v-model="password" type="password"/>
			</div>
			<div class="common-ui-pop-btn btn-margin">
				<a  @click="back" class="common-btn">返&nbsp&nbsp回</a>
				<a  @click="posOpen" class="common-btn">開&nbsp&nbsp業</a>
				<a  @click="centerOut" class="common-btn">中心結業</a>
            </div>
		</form>
	</div>
</template>
<script>
	require("../../assets/sass/login.sass")
    import { mapGetters } from 'vuex'
	import { check_input_null , postRequest } from '../../vuex/utils.js'
	export default {
		computed: {
			...mapGetters([
				'tip_msg',
				'usreInfo'
			])
		},
		data () {
			return {
				staffName:'',
				shopName:'',
				posCode:'',
				staffNumber:'',
				saleDate:'',
				password:''
			}
		} , 
        created:function(){
			this.shopName = this.usreInfo.shopName ; 
			this.posCode = this.usreInfo.posCode ; 
			this.staffNumber = this.usreInfo.staffNumber ; 
			this.saleDate = this.usreInfo.saleDate ; 
			this.staffName = this.usreInfo.staffName ; 
        },
	    activated () {
			this.$store.commit('UPDATE_COMMON_MENU' , false );
			this.password='';
        } , 
		methods : {
            posOpen () { 
            	var vm = this;
        		if ( check_input_null( vm , vm.tip_msg.publicTips.err_05 , 'password' ) ) return;
            	postRequest( {
    				vm : vm,
            		params : {} , 
            		service : 'login',
            		successCallBack : function ( resp ) {
						vm.$store.commit('USER_INFO' , resp ); 
						vm.$router.push('meun_main');
            		},
            		failureCallback : function ( resp ) { 
						vm.$store.commit('UPDATE_LOADING' , true );
            		}
            	} );
			} , 
			back () {
            	this.$router.back();
			} , 
			centerOut () {
            	this.$router.push('login_graduation_center');
			}
		}
	}
</script>



