<template>
    <div class="login-informaction">
        <div>
            <span>店鋪名稱:</span>
            <span v-text="shopName"></span>
        </div>
        <div >
            <span>POS機編號:</span>
            <span v-text="posCode"></span>
        </div>
        <div>
            <span>本班職員:</span>
            <span v-text="staffName"></span>
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex'
    export default {
        computed:mapGetters({
            usreInfo:'usreInfo'
        }),
        data () {
            return {
                staffName:'',
                shopName:'',
                posCode:''
            }
        } , 
        created:function(){
            this.shopName = this.usreInfo.shopName ;
            this.posCode = this.usreInfo.posCode ; 
            this.staffName = this.usreInfo.staffName ; 
        },
    }
</script>


