<template>
	<div class="content-section" id="meun_sub">
        <v-meunUserInfo></v-meunUserInfo>
        <div class="menuButList-warp">
			<div class="menuButList">
				<div class="but-column">
					<div class="but-warp">
						<div @click="goPageSubMeun('meun_join')" class="main-but">
							<span class="subMenu-font-style">加入</span>
						</div>
					</div>
					<div class="but-warp">
						<div @click="goPageSubMeun('meun_order')" class="main-but">
							<span class="subMenu-font-style">會員轉<br>直銷商</span>
						</div>
					</div>
					<!-- <div class="but-warp"><div class="main-but transparent"></div></div> -->
				</div>
			</div>
        </div>
	</div>
</template>
<script>
	require("../../assets/sass/meun_sub.sass")
    import { mapGetters } from 'vuex'
    import meunUserInfo from '../../components/meunUserInfo'
	export default {
		data () {
			return {
			}
		} , 
        components:{
            'v-meunUserInfo' : meunUserInfo 
        },
		methods : {
			goPageSubMeun ( subName) {
				if ( subName ) {
					this.$router.push(subName);
				}
			}
		}
	}
</script>


