<template>
    <transition name="animation-tips">
        <div  v-show="showSummaryInfo" class="common-ui-pop_move" data-pop>
            <div v-on:touchstart="popTouchstart($event)" v-on:touchmove="popTouchmove($event)" ref="pop_move" class="common-ui-pop-content_move">
                <table>
                    <tbody>
                        <tr>
                            <td>總數量</td>
                            <td class="total-quantity">{{orderSummaryInfo.totalQty}}</td>
                        </tr>
                        <tr>
                            <td>訂單售貨額</td>
                            <td class="order-sales">{{orderSummaryInfo.totalBV}}</td>
                        </tr>
                        <tr>
                            <td>訂單積分額</td>
                            <td class="order-integration">{{orderSummaryInfo.totalPV}}</td>
                        </tr>
                        <tr>
                            <td>手續費</td>
                            <td class="service-charge">{{orderSummaryInfo.deliveryCharge}}</td>
                        </tr>
                        <tr class="ui-stress">
                            <td>折扣金額</td>
                            <td class="discount-amount">{{orderSummaryInfo.promotionTotal}}</td>
                        </tr>
                        <tr class="ui-stress">
                            <td>折扣售貨額</td>
                            <td class="discount-sale">{{orderSummaryInfo.promotionBV}}</td>
                        </tr>
                        <tr class="ui-stress">
                            <td>折扣積分額</td>
                            <td class="discount-value">{{orderSummaryInfo.promotionPV}}</td>
                        </tr>
                    </tbody>
                </table>
                <div class="common-ui-pop-btn">
                    <a @click="hideSummaryInfo" class="common-btn">確認</a>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
    import { mapGetters } from 'vuex'
    import { move_pop_touchstart , move_pop_init_startData , move_pop_touchmove } from '../../vuex/utils.js'
    export default {
        props : {
            orderSummaryInfo: {
                type: Object
            },
            isShowSummaryInfo : {
            }
        },
        data () {
            return {
                startData : {
                    initTop : 0,
                    initLeft : 0,
                    startTop : 0 , 
                    startLeft : 0 , 
                    startX : 0 , 
                    startY : 0 
                } ,
                showSummaryInfo : false
            }
        } , 

        watch : {
            'isShowSummaryInfo' (to , from) { 
                if ( to == false ) {
                    this.showSummaryInfo = false;
                } else {
                    this.showSummaryInfo = true;
                }
            }
        } , 
        methods:{
            hideSummaryInfo () {
                this.init_move_coordinate() ;
                this.showSummaryInfo = false;
            } , 
            init_move_coordinate ( ) {
                var vm = this;
                setTimeout(function(){
                    move_pop_init_startData(  vm.$refs.pop_move , vm.startData) ; 
                },500);
            } , 
            popTouchstart ( evt ) {
                move_pop_touchstart( evt , this.$refs.pop_move , this.startData ); 
            } , 
            popTouchmove ( evt ) {
                move_pop_touchmove(evt , this.$refs.pop_move , this.startData ) ; 
            }
        }
    }
</script>



