import * as types from "../types"

const state = {
    // 人員信息
    usreInfo : {
        "regionCode":"130",     //当前地区代码
        "supFlag":"",           //是否主任
        "posCode":"",           //当前POS
        "shopCode":"",          //当前登录中心编号
        "shopName":"",          //当前登录中心名称
        "saleDate":"",          //当前系统日期    当前营业日期
        "staffNumber":"",       //员工编号
        "staffName":"",         //员工姓名
        "strUUID":""            //请求接口的公共参数 
    },
    // 公共請求參數
    publicRequestUsreInfo : {
        "staffNumber":"",
        "regionCode":"",
        "shopCode":"",
        "posCode":"",
        "formateDate":"yyyy-MM-dd"
    },
    // 訂單請求參數
    orderParams:{

    } 
} 
const actions = {
    getBannerList:function({commit}){
        // commit(types.COM_LOADING_STATUS,true);
        // api.getBannerList(function(res){
        //     commit(types.HOME_GET_BANNER_LIST,res.data);
        //     commit(types.COM_LOADING_STATUS,false);
        // })
    },
    getNowPlaying:function({commit}){
        // commit(types.COM_LOADING_STATUS,true)
        // api.getNowPlaying(function(res){
        //     commit(types.HOME_GET_NOWPLAYING_LIST,res.data)
        //     commit(types.COM_LOADING_STATUS,false)
        // })
    },
    getComingSoon:function({commit}){
        // commit(types.COM_LOADING_STATUS,true)
        // api.getComingSoon(function(res){
        //     commit(types.HOME_GET_COMINGSOON_LIST,res.data)
        //     commit(types.COM_LOADING_STATUS,false)
        // })
    }
}
const getters = {
    publicRequestUsreInfo: state => state.publicRequestUsreInfo,
    usreInfo: state => state.usreInfo,
    orderParams: state => state.orderParams,
}

const mutations = {
    [types.USER_INFO](state,res){
        if ( Object.prototype.toString.call(res).split(' ')[1].slice(0,-1) === "Object") {
            state.usreInfo = Object.assign( state.usreInfo,res ) ; 
        }
    },
    [types.ORDER_PARAMS](state,res){
        if ( Object.prototype.toString.call(res).split(' ')[1].slice(0,-1) === "Object") {
            state.orderParams = Object.assign( state.usreInfo,res ) ; 
        }
    },
    [types.PUBLIC_REQUEST_USREINFO](state,res){
        if ( Object.prototype.toString.call(res).split(' ')[1].slice(0,-1) === "Object") {
            state.publicRequestUsreInfo = Object.assign( state.usreInfo,res ) ; 
        }
    }
}
export default {
    state,
    actions,
    getters,
    mutations
}


